@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin: 0;
  margin-bottom: 2.5rem;
}

input {
  border: 1px solid #333;
  border-radius: 0.3em;
  padding: 0.25em;
  width: 7em;
  font-size: 1.4rem;
}

select {
  margin-left: 0.5rem;
  font-size: 1.4rem;
  cursor: pointer;
  border-radius: 5px;
}

/* LOADER */
.loader {
  height: 15px;
  width: 105px;
  display: flex;
  position: relative;
}
.loader .circle {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgb(0, 0, 0);
  animation: move 500ms linear 0ms infinite;
  margin-right: 30px;
}
.loader .circle:first-child {
  position: absolute;
  top: 0;
  left: 0;
  animation: grow 500ms linear 0ms infinite;
}

.loader .circle:last-child {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 0;
  animation: grow 500ms linear 0s infinite reverse;
}

@keyframes grow {
  from {
    transform: scale(0, 0);
    opacity: 0;
  }
  to {
    transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes move {
  from {
    transform: translateX(0px);
  }
  to {
    transform: translateX(45px);
  }
}

/* LOADER / */

.dashboard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 15px 0;
}

.btn {
  cursor: pointer;
  margin: 0 25px;
  font-size: 22px;
  border: 0;
  background-color: rgb(255, 100, 100);
  padding: 10px 10px;
  border-radius: 10px;
  color: #fff;
  min-width: 50px;
  transition: 0.3s;
  outline: none;
}

.btn:hover {
  background-color: rgb(255, 0, 0);
}